.timeline {
  margin-top: 1rem;
}

.timeline__wrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.timeline__line {
  height: 4px;
  background: #405089;
  width: 100%;
  border-radius: 5px;
  position: absolute;
  bottom: 3px;
}

.timeline__list {
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.timeline__list::-webkit-scrollbar {
  display: none;
}

.timeline__item {
  color: #666666;
  font-size: 0.875rem;
  font-weight: 400;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-content: center;
  align-items: center;
  max-width: 4rem;
  line-height: 1.1;
  margin-left: 15%;
