  z-index: 2;
}

.timeline__item:first-child {
  margin-left: 0;
}

.timeline__dot {
  background: #ffffff;
  border-radius: 50%;
  border: 2px solid #405089;
  width: 10px;
  height: 10px;
  margin-top: 0.25rem;
}

.footer {
