  margin-left: 1.5rem;
}

.navigation__item:after {
  content: ' ';
  position: absolute;
  left: 0;
  bottom: -3px;
  height: 2px;
  background: #ffffff;
  width: 0;
  transition: all ease 0.2s;
}

.navigation__item:hover:after, .navigation__item.active:after {
  width: 1.5rem;
