}

.navigation--revers .navigation__item:hover:after, .navigation--revers .navigation__item.active:after {
  background: #405089;
}

.navigation--revers .btn--white {
  background: #405089;
  color: #ffffff;
  border-color: #405089;
}

.navigation--revers .btn--white:hover {
  background: #ffffff;
  color: #405089;
}

.navigation--revers .btn--blue {
  background: #ffffff;
  color: #405089;
  border-color: #ffffff;
}

.navigation--revers .btn--blue:hover {
