}

.subnavigation__item--grey {
  border: 1px solid #eeeeee;
}

.subnavigation__item--grey.active, .subnavigation__item--grey:hover {
  background: #f7f7f8;
}

.subnavigation__link {
  padding: 0.375rem 1rem;
  font-weight: 400;
  display: flex;
  align-items: center;
  color: #c7c6c6;
  transition: all ease 0.3s;
