// Main
.main {
	width: 100%;
	height: 100%;
	min-height: 100vh;
	background-image: url(../img/topBg.jpg);
	background-position: center center;
	background-size: cover;
	&__content {
		max-width: 40rem;
		margin-left: auto;
		padding-top: calc(50vh - 320px); }

	&__icon {
		fill: #ffffff;
		margin-top: 2rem;
		max-width: 3.5rem;
		max-height: 3.5rem;
		margin-bottom: 0.5rem;
		@include rmin($sm) {
			max-width: 4.25rem;
			max-height: 4.25rem; } }
	&__title {
		font-size: 2.5rem;
		color: #ffffff;
		font-weight: 700;
		line-height: 3rem;
		margin-bottom: 1.5rem;
		@include rmin($md) {
			font-size: 4.375rem;
			line-height: 5rem;
			margin-bottom: 2rem; } }
	&__subtitle {
		font-size: 1.75rem;
		color: 2rem;
		font-weight: 400;
		color: #ffffff;
		line-height: 1.3;
		margin-bottom: 2rem;
		@include rmin($sm) {
			font-size: 2rem; } } }
// search
.search {
	width: 100%;
	margin-bottom: 2rem;
	&__group {
		position: relative; }
	&__input {
		width: 100%;
		padding: 1rem 5rem;
		border-radius: 30px;
		font-size: 0.875rem;
		color: #9ea3a8;
		height: 3rem;
		@include rmin($sm) {
			height: 3.5rem; } }
	&__button {
		position: absolute;
		top: 0;
		right: 0;
		border-radius: 30px;
		padding: 0.875rem 2rem;
		color: #ffffff;
		height: 3rem;
		text-indent: -9999px;
		background-image: url(../img/icon/search.svg);
		background-repeat: no-repeat;
		background-position: center center;
		background-color: transparent;
		@include rmin($sm) {
			@include gh(#5EB6FE,#62d1fd);
			font-size: 1.25rem;
			text-indent: 0;
			height: 3.5rem;
			padding: 0.875rem 3rem; } }
	&__country {
		position: absolute;
		left: 1.5rem;
		top: 0.75rem;
		@include rmin($sm) {
			top: 1rem; } } }



// Navigation
.navigation {
	padding-top: 1rem;
	padding-bottom: 1rem;
	&__wrap {
		display: flex;
		align-items: center; }
	&__list {
		margin-left: 2.5rem;
		display: none;
		@include rmin($sm) {
			display: flex; } }
	&__item {
		display: block;
		position: relative;
		margin-left: 1.5rem;
		&:after {
			content: ' ';
			position: absolute;
			left: 0;
			bottom: -3px;
			height: 2px;
			background: #ffffff;
			width: 0;
			@include tr(all ease 0.2s); }
		&:hover:after, &.active:after {
			width: 1.5rem; } }
	&__link {
		color: #ffffff;
		font-weight: 400; }

	// right
	&__right {
		margin-left: auto;
		display: none;
		@include rmin($sm) {
			display: block; } }

	// revers
	&--revers &__link {
		color: #606060; }
	&--revers &__item:hover:after, &--revers &__item.active:after {
		background: $blue; }
	&--revers .btn--white {
		background: $blue;
		color: #ffffff;
		border-color: $blue;
		&:hover {
			background: #ffffff;
			color: $blue; } }
	&--revers .btn--blue {
		background: #ffffff;
		color: $blue;
		border-color: #ffffff;
		&:hover {
			background: $blue;
			color: #ffffff; } } }


// subnavigation
.subnavigation {
	padding-top: 3rem;
	&__title {
		font-size: 1.375rem;
		font-weight: 700;
		color: #404040;
		margin-bottom: 2rem;
		text-align: center; }
	&__list {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		border-radius: 30px;
		margin-bottom: 2rem;
		@include rmin($xs) {
			padding: 0 2rem; }
		&--shadow {
			box-shadow: 0 0 10px 3px rgba(0,0,0,.04); }
		&--center {
			justify-content: center; }
		&--of {
			flex-wrap: nowrap;
			overflow-x: auto;
			justify-content: flex-start;
			margin-left: -20px;
			margin-right: -20px;
			border-radius: 0;
			@include rmin($xs) {
				margin-left: 0;
				margin-right: 0;
				border-radius: 25px; }
			@include rmin($md) {
				justify-content: center; } } }
	&__item {
		border-radius: 25px;
		margin-left: 0.75rem;
		margin-right: 0.75rem;
		margin-top: 0.5rem;
		margin-bottom: 0.5rem;
		@include tr(all ease 0.3s);
		@include rmin($xs) {
			margin-left: 1rem;
			margin-right: 1rem; }
		&.active, &:hover {
			background: $orange;
			color: #ffffff;
			fill: #ffffff;
			border-radius: 25px; }
		&--grey {
			border: 1px solid #eeeeee; }
		&--grey.active, &--grey:hover {
			background: #f7f7f8; } }
	&__link {
		padding: 0.375rem 1rem;
		font-weight: 400;
		display: flex;
		align-items: center;
		color: #c7c6c6;
		@include tr(all ease 0.3s);
		&--grey {
			padding: 0.375rem 1.5rem; }
		& span {
			margin-left: 0.5rem;
			color: #dadbdd; } }
	&__icon {
		width: 1.75rem;
		height: 1.75rem;
		margin-right: 1rem;
		border-radius: 0 !important;
		fill: #c7c6c6;
		@include tr(all ease 0.3s);
		&--close {
			fill: #cccccc;
			width: 0rem;
			height: 1rem; } }
	&__item.active &__link, &__item:hover &__link,
	&__item.active &__icon, &__item:hover &__icon {
		fill: #ffffff;
		color: #ffffff; }
	&__item--grey.active &__link, &__item--grey:hover &__link,
	&__item--grey.active &__icon, &__item--grey:hover &__icon {
		color: #dadbdd;
		& span {
			color: #c7c6c6; } }
	&__item--grey.active &__icon {
		width: 1rem; } }


// mcontent
.mcontent {
	&__title {
		color: #666666;
		font-weight: 400;
		font-size: 1.125rem;
		margin-bottom: 1.5rem; }
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		flex-flow: row wrap;
		justify-content: space-between;
		margin-bottom: 2rem;
		&:after {
			content: "";
			width: 0;
			@include rmin($xs) {
				width: calc(50% - 0.5rem); }
			@include rmin($md) {
				width: calc(33% - 0.5rem); } } }
	&__tour {
		width: 100%;
		@include rmin($xs) {
			width: calc(50% - 0.5rem); }
		@include rmin($md) {
			width: calc(33% - 0.5rem); } }
	&__guide {
		width: 100%;
		margin-bottom: 2rem; } }


// tours
.tour {
	margin-bottom: 1rem;
	border-radius: 30px;
	padding: 1rem 0;
	@include tr(all ease 0.3s);
	@include rmin($xs) {
		padding: 1rem;
		box-shadow: 0 0 10px 3px rgba(0,0,0,.04);
		&:hover {
			box-shadow: 0 5px 15px 5px rgba(0,0,0,.1); } }
	&__image {
		margin-bottom: 0.75rem;
		& img {
			border-radius: 30px;
			overflow: hidden; } }
	&__title {
		display: block;
		font-size: 1.125rem;
		font-weight: 500;
		color: #404040;
		margin-bottom: 0.5rem; }
	&__timeline {
		margin-bottom: 1rem; }
	&__info {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between; }
	&__timing {
		width: 30%;
		&-icon {
			width: 1rem;
			height: 1rem;
			fill: #666666; }
		&-time {
			font-size: 0.925rem;
			color: #666666; } }
	&__price {
		width: 30%;
		line-height: 1.1;
		&-start {
			font-size: 0.875rem;
			color: #666666; }
		&-unit {
			font-size: 0.875rem;
			color: #666666; }
		&-price {
			font-size: 1.25rem;
			color: #404040;
			font-weight: 500; } }
	&__description {
		margin-bottom: 1rem;
		color: #75797e; }
	&__more {
		width: 100%;
		margin-top: 1rem;
		font-size: 0.875rem;
		min-width: auto;
		padding: 0.5rem 0.625rem;
		@include rmin($sm) {
			width: auto;
			margin-top: 0; } } }

// guide
.guide {
	&__wrap {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between; }
	&__photo {
		max-width: 8.25rem;
		width: 100%;
		margin-bottom: 1rem;
		& img {
			border-radius: 25px; } }
	&__description {
		width: 100%;
		@include tr(all ease 0.3s);
		border-bottom: 1px solid #cccccc;
		padding: 0 0 2rem 0;
		@include rmin($xs) {
			border: 0;
			border-radius: 25px;
			padding: 1rem 2rem;
			box-shadow: 0 0 10px 3px rgba(0,0,0,.04); }
		@include rmin($sm) {
			width: calc(100% - 11rem); } }
	&:hover &__description {
		@include rmin($xs) {
			box-shadow: 0 5px 15px 5px rgba(0,0,0,.1); } }

	&__title {
		font-size: 1.325rem;
		font-weight: 400;
		color: #000000;
		margin-bottom: 0.25rem; }
	&__subtitle {
		font-weight: 400;
		color: #75797e;
		margin-bottom: 1rem; }
	&__text {
		color: #75797e;
		margin-bottom: 0.5rem; }
	&__more {
		display: flex;
		font-size: 0.75rem;
		font-weight: 700;
		text-transform: uppercase;
		position: relative;
		color: #000000;
		justify-content: flex-end;
		&:after {
			content: ' ';
			position: absolute; } } }
